body {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  font-family: "Times New Roman", Times, serif;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: lightblue;
}

.app {
  padding: 200px 5%;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.signin-form input[type="text"], input[type="password"] {
  width: 100%;
  margin: 4px 0;
  padding: 10px 10px;
  border-radius: 6px;
  border: 1px #d2cdcd solid;
  background-color: white;
}
.shown {
  display: inline-block;
}

.notShown {
  display: none;
}

.profile {
  width: 100%;
  padding: 5% 10%;
  margin: 7% auto;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  background-color: white;
}

.profilePicture {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 5% auto;
  background-color: grey;
}



